import React, { Component, useState } from 'react';
import logo from './logo.svg';
import './App.css';
import AComponets from './Components/AComponets';
import BComponets from './Components/BComponets';
import MovieContext from './Context/MovieContext';

function App() {
  const[movie,setMovie]=useState('Avengers')

    return (
      <div className="App">
        <MovieContext.Provider value={{studName:"shreyas",movie,setMovie}}>
          <AComponets />
          <BComponets />
        </MovieContext.Provider>
      </div>
    );

}

export default App;



