import React, { useContext } from 'react'
import MovieContext from '../Context/MovieContext'

function BComponets() {
  const {studName,movie,setMovie} = useContext(MovieContext)
  return (
    <div>
      <hr />
      BComponets
      <h2>the stud name is {studName}</h2>
      <input type="text" placeholder='enter movie name' onChange={e=>{setMovie(e.target.value)}} />
    </div>
  )
}

export default BComponets


