import React, { useContext, useState } from 'react'
import MovieContext from '../Context/MovieContext'

function AComponets() {
   
    const {studName,movie} = useContext(MovieContext)
  return (
    <div>AComponets
        <h1>{movie}</h1>
        <h2>The Student name is {studName}</h2>
    </div>
  )
}

export default AComponets


